body {
  background-color: #7cc6fe;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  color: white;
}

.app {
  background-color: #252d4a;
  width: 500px;
  min-height: 250px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}

.score-section {
  text-align: center;
}

.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  color: #7cc6fe;
  font-size: 14px;
}

.question-text {
  color: #7cc6fe;
  margin-bottom: 12px;
  font-size: 24px;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
}

button:hover {
  background-color: #555e7d;
  color: white;
}

button.correct:hover {
  background-color: #2f922f;
}

button.incorrect:hover {
  background-color: #e74c3c;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}
.actions {
  display: flex;
  margin-top: 15px;
}
.actions button {
  background-color: #7cc6fe;
  justify-content: center;
}
.actions button:hover {
  background-color: #234668;
  color: white;
}

/* Small devices (mobile phones) */
@media only screen and (max-width: 600px) {
  .app {
    width: 90%;
  }
}

/* Medium devices (tablets) */
@media only screen and (min-width: 600px) and (max-width: 992px) {
  .app {
    width: 70%;
  }
}
